.FormOffline-window {
  padding: 1.15em; }

.FormOffline {
  display: flex;
  flex-direction: column; }

.FormOffline-text {
  text-align: center;
  font-size: 1em;
  padding: 0.2em 0 2em; }

.FormOffline-block {
    display: block;
    margin-bottom: 1.1em;
    &__label {
      font-size: 1.16em; } }

.FormOffline-block__input, {
  margin: .5em 0 0.2em;
  width: 100%;
  font-size: 1.2em;
  padding: 0.375em 0.5em; }

.FormOffline-block__input {
  color: #525252;
  border: 1px solid gainsboro;
  border-radius: 1px;
  &__error {
    border: 1px solid #de0a23;
    outline: none; } }

.FormOffline-block__errorMessage {
  color: #de0a23;
  font-size: 0.81em;
  position: relative;
  display: block;
  padding-left: 18px;
  &:before {
    content: '';
    display: block;
    position: absolute;
    width: 15px;
    height: 15px;
    left: 0;
    background-image: url('../../assets/img/error-15x15.png');
    background-repeat: no-repeat; } }


.FormOffline-block__select {
  background-color: transparent;
  cursor: pointer;
  appearance: none; }
.FormOffline-block__pseudoElem {
  position: relative;
  &:before {
    content: "";
    display: block;
    background: transparent;
    position: absolute;
    right: 9px;
    top: 40px;
    width: 8px;
    height: 8px;
    border: {
      right: 2px solid  #525252;
      bottom: 2px solid  #525252; }
    transform: rotate(45deg);
    cursor: pointer; } }

.FormOffline-buttons {
  display: flex;
  justify-content: center;
  .FormOffline-button {
    display: block;
    width: 100%;
    text-transform: uppercase;
    border-radius: 2px;
    padding: .8em 0;
    border: none;
    cursor: pointer;
    font-size: 1em;
    &__cancel {
      color: #525252;
      background-color: #fff;
      margin-right: 6px; }
    &__start {
      color: #fff;
      background-color: #b80635;
      margin-left: 6px;
      &:disabled {
        background-color: #eee;
        color: #000; } } } }

.textarea {
  width: 100%;
  resize: vertical;
  min-height: 60px;
  padding: 5px;
  font-style: italic;
  margin-top: 10px; }
